      <TableOptions class="table-options" v-if="showOptions" :name="$localizeFilter('type')" :options="types" :prefix="'operations_'" @selectOption='selectOption'/>
  </div>
  <div class="custom-table"  v-if="showTable">
    <div class="custom-table-header">
      <div class="custom-table-row light header">
        <div class="custom-data-cell cell-1 header mobile">{{ $localizeFilter('operation_name_amount')}}</div>
        <div class="custom-data-cell cell-1 header desktop">{{ $localizeFilter('operation_name') }}</div>
        <div class="custom-data-cell cell-2 header desktop">{{ $localizeFilter('operation_amount') }}</div>
        <div class="custom-data-cell cell-3 header">{{ $localizeFilter('operation_date') }}</div>
      </div>
    </div>

    <div class="custom-table-body">
        <div class="custom-table-row" v-for="(operation, idx) in operations"  :key="idx" :class="getClass(idx)">
            <div class="custom-data-cell cell-1">
                <div class="cell-1-content mobile">
                    <div class="percent-amount-block">
                        <!-- <div class="percent-mark" v-if="operation.percent">{{ (operation.percent * 100).toFixed(2) + '%'}}</div> -->
                        <p class="operation-amount">{{ operation.amount.toFixed(2) + ' ' + (operation.coin)}}</p>
                    </div>
                    <p class="operation-name">{{ operation.name ?? '-' }}</p>
                </div>
                <div class="cell-1-content desktop">
                    <p class="operation-name">{{ operation.name  ?? '-' }}</p>
                    <!-- <div class="percent-mark" v-if="operation.percent">{{ (operation.percent * 100).toFixed(2) + '%'}}</div> -->
                </div>
            </div>
            <div class="custom-data-cell cell-2 desktop"><p class="operation-amount">{{ operation.amount.toFixed(2) + ' ' + (operation.coin)}}</p></div>
            <div class="custom-data-cell cell-3"><p>{{$dateTimeFilter(operation.createdTimestamp, 'datetime')}}</p></div>

        </div>
    </div>
  </div>
  </div>
</template>

<script>
import TableOptions from "@/components/TableOptions.vue"
export default {
  props: [
    'operations', 'showOptions', 'showTable'
  ],
  components: {
    TableOptions
  },
  data: () => ({
    types: ['all','replenish','dividend'],
    type: 'all'
  }),
  methods: {
      selectOption(idx) {
        this.type = this.types[idx]
        this.$emit('changeStatus', this.type)
      },
      getClass(idx) {
        return ((idx % 2 == 0)? 'dark': 'light')
      }
  },
}
</script>

