<template>
    <div class="balance">
        <div class="balance-block">
            <label class="balance-title">{{$localizeFilter('balance') + ':'}}</label>
            <div class="amount-block">
                <label class="balance-amount">{{ balance ?? '0' }}</label>
                <label class="balance-currency">{{coin}}</label>
            </div>
            <button class="basic-button" v-if="canBuy==true" @click="fillBalance">{{$localizeFilter('buy_coin') + ' ' + coin}}</button>
        </div>
        <div class="buttons-block">
            <button class="basic-button" @click="handleClick">{{type=='balance' ? $localizeFilter('withdrawal_applications') : $localizeFilter('fill_sigma')}}</button>
            <button class="basic-button" v-if="type=='balance'" @click="withdrawFromBalance">{{type=='balance' ? $localizeFilter('withdrawal') : $localizeFilter('withdraw_from_sigma')}}</button>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: {
        type: {
            type: String,
            default: 'balance' //  'balance', 'sigma'
        },
        coin: {
            type: String,
            required: true
        },
        canBuy: {
            type: Boolean,
            default: false
        }
    },
    data: ()=>({
        balance: 0,
        walletBalance: 0
    }),
    watch:{
        coin(newVal, oldVal) {
            this.$nextTick(() => { this.loadData() });
        }
    },
    setup() {
        
    },
    methods: {
        fillBalance() {
            this.$emit('fillBalance')
        },
        withdrawFromBalance() {
            this.$emit('withdrawFromBalance')
        },
        showWithdrawalRequests() {
            if (this.type=='balance') {
                this.$emit('showWithdrawalRequests')
            }
        },
        handleClick() {
            if (this.type=='balance') {
                this.showWithdrawalRequests()
            } else {
                this.fillBalance()
            }
        },
        async loadData() {
            try {
                const data = await this.$store.dispatch('getBalance', {currencyCode: this.coin})
                this.balance = data.balance.toFixed(2)
                this.$emit('setBalance', this.balance)
            } catch(e) {
                this.$error(e)
            } 
        }
    },
     async mounted() {
        this.loadData()
    }
})
</script>

