<template>
  <div class="bonus-operations">
      <p class="title">{{$localizeFilter('operations_on_account')}}</p>
      <BonusOperationsTable 
      :showTable="operations.length > 0"
      :operations="operations" :showOptions="false" @changeStatus='changeStatus'/>
      <EmptyTable v-if="operations.length == 0" :text="$localizeFilter('no_operations')"/>
      <pagination v-model="page" :records="count" :per-page="pageSize" @paginate="pageChangeHandler"/>
  </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import BonusOperationsTable from '@/components/BonusOperationsTable'
import paginationMixin from '@/mixins/pagination.mixin'
import localizeFilter from "@/filters/localize.filter"
import EmptyTable from "@/components/EmptyTable"

export default defineComponent({
  props: ['type','showOptions','coin'],
  components: {
    BonusOperationsTable, EmptyTable
  },
  mixins: [paginationMixin],
  watch:{
      page() {
           this.$nextTick(() => { this.loadData() });
      },
      coin(newVal, oldVal) {
           this.$nextTick(() => { this.loadData() });
      }
  },
  setup() {
    
  },
  data: () => ({
    operations: [],
    count: 0,
    status: 'all'
  }),
  mounted() {
    this.loadData()
  },
  methods: {
    changeStatus(status) {
      this.status = status
      this.loadData()
    },
    async loadData() {
        try {
            // // console.log(this.coin)
            const data = {status: this.status, skip: this.skip, limit: this.limit, currencyCode: this.coin || 'GDC'}
            this.operations = await this.$store.dispatch('getBalanceOperationsByFilter', data)
            this.operations.forEach(item => {
                item.coin = this.coin;
            })
            const {count} = await this.$store.dispatch('getBalanceOperationsByFilterCount', data)
            this.count = count
            this.setupPagination(this.operations)
        } catch(e) {
            this.$error(e)
        } 
     }
  }
})
</script>

